import axios from 'axios'

const weatherApi = axios.create({
  withCredentials: true,
  headers: {
    'Accept': 'application/json',
    'Content-Type': 'x-www-form-urlencoded',
    'Access-Control-Allow-Origin': 'https://www.metaweather.com'
  },
})

axios.defaults.withCredentials = true;
axios.defaults.crossDomain = true;
axios.defaults.headers.common['Access-Control-Allow-Origin'] = 'https://www.metaweather.com';

// axios.defaults.headers
const path = 'https://www.metaweather.com'; // for github pages

const api = {
  getLocationByQuery: (query) => axios.get(`${path}/api/location/search/?query=${query}`),
  getLocationByCoords: ({lat, lng}) => weatherApi.get(`${path}/api/location/search/?lattlong=${lat},${lng}`),
  getForecastByCityId: (id) => axios.get(`${path}/api/location/${id}/`),
};

export default api